* {
    margin: 0px;
    padding: 0px;
    text-decoration: none;
    box-sizing: border-box;
    list-style: none;
    font-family: 'Times New Roman', Times, serif;
    color: black;
}


html {
    font-size: 16px;
}


.container {
    display: flex;
    margin: auto;
    width: 100%;
    min-height: 100vh;
    height: 100%;
    background: radial-gradient(#8e4aff, #8e4aff, #8e4aff, #8e4aff, #8e4aff);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    align-items: flex-start;
    justify-content: center;
}



.container-template {
    width: 1450px;
    height: 100%;
    display: flex;
    align-items: flex-start;
    justify-content: space-around;
}



.container-search {
    display: flex;
    width: 45%;
    margin: 40px 0px 40px 0px;
    color: white;
    border-radius: 20px;
    justify-content: center;
    background-color: rgb(255, 255, 255);
    padding-right: 40px;
    padding-left: 40px;
    padding-bottom: 25px;
    flex-wrap: wrap-reverse;
}


.weather-data {
    display: flex;
    width: 100%;
    height: 40px;
    align-items: center;
    justify-content: space-between;
    border-bottom: 2px solid #8e4aff;
    padding: 5px 10px 5px 10px;
    font-weight: bold;
    margin: 10px auto;
}


.weather-data-container {
    border-radius: 15px;
    box-shadow: 0px 0px 8px #8e4aff;
    padding: 15px;
    width: 100%;
}



.container-location {
    width: 50%;
    margin: 40px 0px 40px 0px;
}




.searchbox {
    width: 350px;
    height: 30px;
    margin: 23px auto;
    outline: none;
    border-radius: 25px;
    border: 3px solid #8e4aff;
    color: rgb(0, 0, 0);
    padding: 20px 20px;
    font-size: 1rem;
    font-weight: bold;
    text-align: center;
    background: transparent;
}


.location-name {
    width: 250px;
    height: 40px;
    box-shadow: 0px 0px 5px #ffffff;
    border: 2px solid white;
    margin-left: 10px;
    padding: 5px 10px 10px 7px;
    border-radius: 23px;
    text-align: center;
    font-weight: bold;
    font-size: 1rem;
    color: white;
}


.date {
    background-color: white;
    text-align: center;
    border-radius: 15px;
    max-width: 230px;
    padding: 10px 20px;
    float: right;
    margin-right: 20px;
    margin-top: 25px;
    font-weight: bold;
}


.today {
    font-size: 2.5rem;
    color: black;
}

.dateval {
    font-size: 1.2rem;
    font-weight: bold;
    margin-top: 10px;
}

.location-temperature {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 180px;
    padding: 50px;
    border: 2px solid white;
    border-radius: 25px;
    background-color: white;
    max-width: 520px;
    width: 100%;
    max-height: 300px;
    font-weight: bold;
}


.temperature {
    font-size: 4rem;
    font-weight: bold;
    color: rgb(0, 0, 0);
}

.min-temp,
.max-temp {
    font-size: 1.05rem;
    font-weight: bold;
    color: rgb(113, 5, 255);
    padding: 5px 5px 0px 0px;

}


h1 {
    text-align: center;
    color: #000000;
    margin-top: 23px;
}

h2 {
    margin-top: 30px;
    text-align: center;
    font-size: 2.3rem;
    color: #000000;

}

button {
    height: 35px;
    width: 150px;
    font-size: 1.2rem;
    background-color: white;
    color: #8e4aff;
    display: block;
    margin: auto;
    margin-top: 70px;
    border: 1px solid rgb(255, 255, 255);
    font-weight: bold;
    border-radius: 5px;
}

button:hover {
    box-shadow: 0px 0px 7px #ffffff;
}

h4 {
    text-align: center;
    color: #000000;
    margin-bottom: 1.9rem;
    font-size: 1.1rem;
}

h1 {
    margin-top: 1.2rem;
    color: #8e4aff;
    font-size: 3rem;
}


.location-icon {
    width: 20px;
    height: 20px;
    -webkit-filter: invert(100%);
            filter: invert(100%);
    margin-right: 10px;
}

.weather-icon {
    max-width: 110px;
    width: 100%;

}

.cloud-icon {
    font-size: 1.2rem;
    text-align: center;

}



/* MEDIA QUERY STARTS HERE: */

@media(max-width:1050px) {

    .container-template {
        flex-direction: column;
        align-items: center;

    }

    .container-search {
        width: 70%;
    }

    .container-location {
        width: 70%;
    }
}


@media(max-width:850px) {

    html {
        font-size: 13px;
    }



    .container-search {
        width: 80%;
        margin-top: 30px;

    }

    .container-location {
        width: 80%;
    }

    .location-name {
        width: 170px;
        height: 30px;
        padding: 3px 0px 0px 0px;

    }

    .location-icon {
        height: 15px;
        width: 15px;
    }

    .location-temperature {
        margin-top: 115px;
        max-width: 400px;
        padding: 20px 30px;
    }

}



@media(max-width:500px) {

    html {
        font-size: 10px;
    }

    .location-temperature {
        flex-direction: column;
        max-width: 300px;
        margin: 100px auto auto auto;
    }

    .temperature {
        margin-top: 22px;
    }

    .weather-data-container {
        width: 100%;
    }

    .searchbox {
        width: 240px;
        height: 20px;
        padding: 10px 10px;
        border: 1.5px solid #8e4aff;
    }

    .container-search {
        width: 90%;
        padding: 10px 5px 20px 5px;
    }

    .location-name {
        width: 130px;
        height: 20px;
        box-shadow: 0px 0px 3px #ffffff;
        border: 1.5px solid white;
        margin-left: 0px;
        padding: 2px 0px 0px 0px;

    }

    .location-icon {
        height: 10px;
        width: 10px;
        margin-left: 3px;
    }

    button {
        height: 20px;
        width: 80px;
        border-radius: 2px;
    }

}
